import { api, tags, urlUtils } from '@igblsln/store';
export const projectInquiryApi = api.injectEndpoints({
    overrideExisting: false,
    endpoints: (build) => ({
        listProjectPurchaseInquiry: build.query({
            query: ({ projectId }) => {
                return {
                    url: urlUtils('/inquiry/purchase/', `?project_id=${projectId || ''}`),
                };
            },
            providesTags: (result = []) => [
                ...result.map(({ key: id }) => ({ type: tags.PROJECT_PURCHASE_INQUIRY, id })),
                { type: tags.PROJECT_PURCHASE_INQUIRY, id: 'LIST' },
            ],
        }),
        getErrorProne: build.query({
            query: () => 'error-prone',
        }),
    }),
});
export const { useListProjectPurchaseInquiryQuery, useGetErrorProneQuery } = projectInquiryApi;
